 
import { mainWebGLRenderer } from 'plugins/webgl/three/index';
/*
** Kontrol store
*/
import { computed, readonly, ref } from 'vue';
import { DEBUG } from 'config';
import { defineStore } from 'pinia';
import { each, filter, find, findIndex, has, isEmpty, keys, last, map, merge, omit, values } from 'lodash';
import { ButtonParams, FolderApi, Pane, TabApi, TabPageApi } from 'tweakpane';
import * as EssentialsPlugin from '@tweakpane/plugin-essentials';
import { InputParams, K_AddOption, K_Folder, K_FolderTarget, K_Param, WatchParams } from "plugins/kontrol/types";
import { pinia } from 'plugins/store/index';
import LocalStorageHelper from 'utils/helpers/LocalStorage';
import config from "./config";
import { SRGBToLinear, hexToGLSL, numberToRgbColor } from './utils';

// Sizes constants (used with keyboard shortcuts)
const SIZES = ['default','large','full'];

//---- Vars
let pane : O<Pane>;
let fpsGraph : any;


interface KontrolStore extends Obj {
  ready: boolean,
  visible: boolean;
  size: string;
  theme: "default" | "dev" | "minimal";
  position: "right" | "left";

  exportOutput?:string;
  importInput?:string;
}
interface UniformObjectValue {
  value: any;
  isColor?: boolean;
}

const useStore = defineStore('Kontrol', () => {
  
  /**
   * Clear eberything on HMR
   */
  if (import.meta.hot) {
    import.meta.hot.on("vite:beforeUpdate", () => {
      clear();
    });
  }

  const state = ref<KontrolStore>({
    ready: false,
    visible: true,
    position: "right",
    theme: config.theme,
    size: "default",
  });

  /**
   * Initialize and create the main tabs
   */
  const init = () => {
    pane = new Pane({
      container: document.querySelector(config.container) as HTMLElement,
    });
    
    let tabs = config.texts.tabs;
    // if(state.value.theme === 'dev') tabs = [config.texts.tabs.main, config.texts.tabs.system];
    const {main, system} = config.texts.tabs
    if(state.value.theme === 'dev') tabs = {main, system};
    if(state.value.theme === 'minimal') tabs = {main};
    
    // Add all Tabs
    pane.addTab({
      pages:map(tabs, tab => ({title:tab})),
    });
    
    // add plugins
    pane.registerPlugin(EssentialsPlugin);

    //Add the main folder
    getRootTab(config.texts.tabs.main).addFolder({title:config.texts.title});
  }

  /**
   * Add the webgl system tab
   */
  const addSystem = () => {
    const systemTab = getRootTab(config.texts.tabs.system);
    if(state.value.theme === 'minimal') return;
    
    //FPS
    if(config.fps) {
      fpsGraph = systemTab?.addBlade({
        view: 'fpsgraph',
        label: 'fps',
      });
    }

    // Add webgl stats only if it's running
    if(mainWebGLRenderer) {
      //-----  Memory Folder
      addParam(mainWebGLRenderer.info.memory, undefined, {
        path: [{folders: ['Memory']}]
      }, config.texts.tabs.system);
  
      //----- Programs Folder
      const programs = {
        total: mainWebGLRenderer.info.programs?.length
      };
      addParam(programs, undefined, {
        path: [{folders: ['Programs']}],
        total: {
          change: () => {
            programs.total = mainWebGLRenderer!.info.programs?.length;
          }
        }
      }, config.texts.tabs.system);
      
      //----- Render
      addParam(mainWebGLRenderer.info.render, undefined, {
        path: [{folders: ['Render']}]
      }, config.texts.tabs.system);
    }
  }

  /**
   * Add the utils Tab
   */
  const addUtils = () => {
    if(state.value.theme !== 'default') {
      state.value.ready = true;
      return;
    }
    
    // --------
    // -------- LocalStrorage Params
    // --------
    const saveParams ={
      //autosave
      save: () => saveInStorage(),
      refresh: () => getStorage(),
      delete: () => clearStorage()
    };

    addParam(saveParams , undefined, {
      path: [ {tabs: ['Local Storage']}],
      save: { title: "Save (Shift+S)" },
      refresh: { title: "Refresh (Shift+R)" },
      delete: { title: "Delete (Shift+D)" }
    }, config.texts.tabs.utils);

    // --------
    // -------- Backup Params
    // --------
    const backupParams = { 
      //import
      import: () => { 
        state.value.importInput = '';
       },
      
      //export
      export: () => exportData(),
    };

    addParam(backupParams, undefined, {
      path: [ {tabs: ['Backup'], status:{ selected:false }}],
      import: { title: "Import" },
      export: { title: "Export" }
    }, config.texts.tabs.utils);

    // --------
    // -------- Color Management Params
    // --------
    const colorManagment = {
      value: '',
      glsl: '',
    }

    addParam(colorManagment, undefined, {
      path: [
        { folders: ['Color Managment'], status: { expanded:false }},
      ],
      value: {
        label: "HEX Color",
        change: (e:any) => {
          colorManagment.glsl = hexToGLSL(e.value);
          if(!colorManagment.glsl.length) colorManagment.glsl = 'error';
        }
      },
      glsl: {
        label: "GLSL Result",
        readonly: true as any,
      },
    }, config.texts.tabs.utils);

    // store is ready
    state.value.ready = true;
  }

  /**
   * Running fps graph
   * @param mode begin or end
   */
  const fps = (mode:string) => {
     
    (mode === 'begin') ? fpsGraph?.begin() : fpsGraph?.end(); 
  }

  /**
   * Add param to the pane
   * @param param Object
   * @param values Optional (if we want to add only these properties)
   * @param options Binding options (see below for each binding customisation)
   * @param tab Can add inside a specific root tab
   */
  const addParam = (param : K_Param, values? : string | string[], options : K_AddOption = {}, tab?:string) : void => {
    const rootTab = getRootTab(tab || config.texts.tabs.main)
    
    //---- Folder managment
    let outputFolder : K_FolderTarget = rootTab;
    if(!tab) outputFolder = rootTab.children[0] as FolderApi;

    // If a folder path is defined
    if(options.path) {
      //save the tab config before it'll be shifted
      let hasConfigTabStatus = last(options.path)?.status?.selected;
      if(hasConfigTabStatus === undefined) hasConfigTabStatus = true;


      outputFolder = getFolder(rootTab, options.path);

      // if a change event handler is defined (only for Folder)
      if(options.change && outputFolder instanceof FolderApi) {
        (outputFolder as FolderApi).on("change", (value) => options.change?.(value));
      }
      
      // select the tab by default (or based by status config)
      if(outputFolder instanceof TabPageApi) {
        outputFolder.selected = hasConfigTabStatus;
      }
    }

    
    // Parse through properties or specific values (arguments values)
    const properties = (typeof values === 'string' ? [values] : values) || keys(param);
    
    //Check if it's an uniform object or not
    const isUniformObject = filter(properties, key => {
      return has(param[key], 'value');
    }).length === properties.length;

    each(properties, (prop:string) => {
      const cleanOptions = omit(options, ['folder']);
      let bindingOptions = merge(isUniformObject ? { label: prop } : {}, cleanOptions?.[prop]) as InputParams | ButtonParams | WatchParams;
      
      // If system -- readonly bindings
      if(tab === config.texts.tabs.system) {
        bindingOptions = merge({readonly:true}, bindingOptions);
      }

      //string https://tweakpane.github.io/docs/input-bindings/#string
      //boolean https://tweakpane.github.io/docs/input-bindings/#boolean
      //color https://tweakpane.github.io/docs/input-bindings/#color
      //point (vec2, vec3) https://tweakpane.github.io/docs/input-bindings/#point
      //number https://tweakpane.github.io/docs/input-bindings/#number
      //function https://tweakpane.github.io/docs/ui-components/#button
      //plugins https://github.com/tweakpane/plugin-essentials
      
      const outputParam = isUniformObject ? (param[prop] as UniformObjectValue).value : param[prop];
      if (outputParam !== undefined && !outputParam?.isTexture) {
        // Function = Add Button
        if (typeof outputParam === 'function') {
          const btn = outputFolder.addButton( merge( {title:"title missing"} , {...bindingOptions as ButtonParams} ));
          btn.on("click", () => outputParam());
        } else {
          // Create binding
          const binding = outputFolder.addBinding(
            isUniformObject ? param[prop] : param, 
            isUniformObject ? 'value' : prop, 
            bindingOptions as InputParams
          );
          
          // Check for color
          if (
            (isUniformObject && has(param[prop] as UniformObjectValue, 'isColor')) ||
            has(param[prop], 'isColor')
          ) {
            binding.tag = "three-color";
          }
  
          // if a change event handler is defined only for this binding
          if ((bindingOptions as WatchParams)?.change && binding) {
            binding.on("change", (value) => (bindingOptions as WatchParams)?.change?.(value))
          }
        }
      }
    });
  }

  /**
   * Show or hide the panel
   * @param value string vlaue
   */
  const togglePosition = (value : string) => {
    state.value.position = value === "ArrowLeft" ? "left" : "right";
  }

  /**
   * Show or hide the panel
   * @param value boolean vlaue
   */
  const toggleVisible = (value : boolean) => {
    state.value.visible = value;
  }

  /**
   * Increase or decrease the panel size
   * @param up boolean vlaue
   */
  const changeSize = (up : boolean) => {
    const index = SIZES.indexOf(state.value.size);
    
    // bigger
    if(up) {
      state.value.size = SIZES[Math.min(SIZES.length - 1, index+1)];
    }
    //s maller
    else{
      state.value.size = SIZES[Math.max(0, index-1)];
    }
  }

  /**
   * Select the next tab 
   * @param direction Direction of the tab nav
   */
  const selectNextTab = (direction: number) => {
    const tabKeys = keys(config.texts.tabs);
    const tabs = map(tabKeys, key => getRootTab(config.texts.tabs[key]));

    let findSelected = findIndex(tabs, tab => tab.selected);
    findSelected += direction;
   
    if(findSelected > tabKeys.length - 1) findSelected = 0;
    if(findSelected < 0) findSelected = tabKeys.length - 1;
    
    tabs[findSelected].selected = true;
  }

  /**
   * Clear The whole panel (erase all bindings but the panel it still here)
   */
  const clear = () => {
    let mainTab = getRootTab(config.texts.tabs.main);
    mainTab.dispose(); // remove main tab

    // Recreate the main tab
    mainTab = (pane?.children[0] as TabApi).addPage({
      title: config.texts.tabs.main,
      index:0
    });

    mainTab.addFolder({title:config.texts.title});
    mainTab.selected = true;
  }

  /**
   * Dispose
   */
  const dispose = () => {
    pane?.dispose();
    pane = undefined;
    state.value.ready = false;
  }

  /**
   * Export all the data
   */
  const exportData = () => {
    state.value.exportOutput = JSON.stringify(getRootTab(config.texts.tabs.main)?.exportState()); 
  }

  /**
   * Import Data
   * @param json String data 
   * @param fromStorage import from LS or not
   */
  const importData = (json:string|null) => {
    if(!json) return;

    // --------------- JSON Parsing
    let data;
    try {
      data = JSON.parse(json);
    }
    catch (error) {
      console.log(error);
    }

    // Search for colors
    const searchForColors = (node? : any) => {
    
      //had binding so check if it's color
      if(node?.tag === 'three-color') {
        const rgb = numberToRgbColor(node.binding.value);
        rgb.r = SRGBToLinear(rgb.r/255);
        rgb.g = SRGBToLinear(rgb.g/255);
        rgb.b = SRGBToLinear(rgb.b/255);
        node.binding.value = rgb;
      }
      
      //recursive search
      if(has(node, 'children')) each(node?.children, item => searchForColors(item));
    }
    // ----- End Color Fixing

    // if empty - stop
    if(isEmpty(data)) return;
    
    // fix colors
    for(let i=0; i< data.children.length; i++) {
      searchForColors(data.children[0]);
    }

    //add to the main tab
    const mainTab = getRootTab(config.texts.tabs.main);
    mainTab?.importState(data);
    
    getRootTab(config.texts.tabs.utils).selected = true;
    mainTab.selected = true;
  }

  /**
   * Clear import and export flags (for Lightboxes watchers)
   */
  const clearImportExport = () => {
    state.value.exportOutput = undefined;
    state.value.importInput = undefined;
  }

  //--------------------------------- Helpers Methods
  
  /**
   * Get one of the global tab
   * @param title 
   * @return The requested tab
  */
  const getRootTab = (title : string) : TabPageApi => {
   const tabs = pane?.children[0] as TabApi;
   return tabs.pages[values(config.texts.tabs).indexOf(title)];
  }
  
  /**
   * Create a folder arborescence based on a path string
   * @param tab The starting point
   * @param path Array of folders and tabs (whatever order)
   * @return Final output folder
  */
  const getFolder = (tab : TabPageApi, paths:K_Folder[]) : K_FolderTarget => {
   let output : K_FolderTarget | undefined;

   while(paths.length) {
      const path = paths[0];
     
      if(path?.folders?.length) {

        // Searching Folders
        while(path.folders.length) {
          const from = output || (tab.title === config.texts.tabs.main ? tab.children[0] as FolderApi : tab);
          output = searchFolder(path.folders[0], from, path?.status);
          path.folders.shift();
        }
      }
        
      // Searching tabs
      if(path?.tabs?.length) {
        const from = output || (tab.title === config.texts.tabs.main ? tab.children[0] as FolderApi : tab);
        output = searchTab(path.tabs, from);
      }
    
      paths.shift();
    }
    
    return output!;
  }
  
  /**
   * Search a folder inside the panel based on a starting point (if doesn't exist = we create it)
   * @param title The requested folder name
   * @param from The starting point to search from (if undefined it's the main tab itself)
   * @param status Check if the last folder must be expanded
  */
  const searchFolder = (title:string, from : FolderApi | TabPageApi, status?:Obj ) : FolderApi => {
    const hasFolder = find(from.children, item => (item as FolderApi).title === title) as FolderApi;
    return hasFolder || from.addFolder({title, expanded:status ? status.expanded : true});
  }
  
  /**
   * Search a tab inside the panel based on a starting point (if doesn't exist = we create it)
   * @param titles The requested folder name
   * @param from The starting point to search from (if undefined it's the main tab itself)
  */
  const searchTab = (titles:string[], from : K_FolderTarget) : TabPageApi => {
    let hasTab : K_FolderTarget | undefined = undefined;
   
    // If the first children of our starting point is a tab
    if(from.children[0] instanceof TabApi) {
      const currentTab = from.children[0] as TabApi;
      hasTab = find(currentTab.pages, tab => tab.title === titles[titles.length-1]);

      // If there is not our tab but the row contains already another tab (we want to be a sibling to the existing one)
      if(!hasTab && currentTab.pages.length) {
          // Recreate the main tab
          hasTab = currentTab.addPage({
            title: titles[titles.length-1],
          });
      }
    }
    
    //return the output tab
    if(hasTab) return hasTab as TabPageApi;
    
    // create tabs
    const newTabs = from.addTab( {
      pages: map( titles, title => ( {title}) )
    });
    
    return newTabs.pages[newTabs.pages.length-1];
  }

  /**
   * Save the main tab in Local storage
   * @return Boolean
   */
  const saveInStorage = () => {
    LocalStorageHelper.set(config.storageKey, JSON.stringify(
      getRootTab(config.texts.tabs.main)?.exportState()
    ));
  }

  /**
   * Clear the Local Storage
   */
  const clearStorage = () => {
    LocalStorageHelper.remove(config.storageKey);
  }

  /**
   * Retrieve the Local Storage and import it
  */
  const getStorage = () => {
    importData(LocalStorageHelper.get(config.storageKey));
  }

  //---------------------------------
  
  return {
    state: readonly(state),
    ready: computed(()=>state.value.ready),
    visible: computed(()=>state.value.visible),
    position: computed(()=>state.value.position),
    size: computed(()=>state.value.size),
    exportOutput: computed(()=>state.value.exportOutput),
    importInput: computed(()=>state.value.importInput),
    theme: computed(()=>state.value.theme),

    init,

    getStorage,
    saveInStorage,
    clearStorage,

    importData,
    
    addParam,
    addSystem,
    addUtils,
    

    clear,
    clearImportExport,
    dispose,

    togglePosition,
    toggleVisible,
    changeSize,
    selectNextTab,

    fps,
  }
})
export const store = useStore(pinia);
export default store;

// @ts-expect-error
if(DEBUG) window.storeKontrol = store
