// ----- RGB Color Fixing
// When we stringify a rgb color component --> the json class renders it as a big int

import { Color } from "three";

// and Tweakpane doesn't recognize it, so let's fix that
export const numberToRgbColor = (num: number): {r:number, g:number, b:number} => {
  return ({
    r: (num >> 16) & 0xff,
    g: (num >> 8) & 0xff,
    b: (num & 0xff)
  })
};

// ----- SRGB To Linear
export const SRGBToLinear = ( c : number ) => {
  return ( c < 0.04045 ) ? c * 0.0773993808 : Math.pow( c * 0.9478672986 + 0.0521327014, 2.4 );
}

// ----- Hex to RGB
export const hexToGLSL = (hex:string) => {
  //check if valid hex value
  if (/^#([0-9A-F]{3}){1,2}$/i.test(hex)) {
    const col = new Color(hex);
    col.convertLinearToSRGB(); 
    const out = col.toArray().map((x) => {
      //to fixed 3
      const conv = Math.round(x * 1000) / 1000;
      //append missing periods
      if (conv.toString().indexOf('.') === -1) return conv + "."
      return conv;
    });
    return `vec3(${out})`;
  } else {
    return '';
  }
}

// ----- RGB To HEX
const channelToHex = (channel:number) => {
  const hexadecimal = channel.toString(16);
  return hexadecimal.length == 1 ? "0" + hexadecimal : hexadecimal;
}

export const RGBtoHex = (r:number, g:number, b:number) => {
  return "#" + channelToHex(r) + channelToHex(g) + channelToHex(b);
}
